<template>
  <div>
    <div class="loading center" v-if="isLoading">
      <looping-rhombuses-spinner
        :animation-duration="2000"
        :size="80"
        color="#7971ea"
      />
    </div>
    <div class="my-5 row justify-content-center">
      <form class="col-md-6" @submit.prevent="payOrder">
        <table class="table table-striped">
          <thead class="bg-secondary text-white">
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-right">總計</td>
              <td class="text-right">{{ order.total }}</td>
            </tr>
          </tfoot>
        </table>

        <table class="table table-striped">
          <tbody>
            <tr>
              <th width="100">Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th>姓名</th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th>收件人電話</th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th>收件人地址</th>
              <td>{{ order.user.addr }}</td>
            </tr>
            <tr>
              <th>付款狀態</th>
              <td>
                <span v-if="!order.is_paid">尚未付款</span>
                <span v-else class="text-success">付款完成</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-right" v-if="order.is_paid === false">
          <button class="btn btn-danger">確認付款去</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      isLoading: false,
      orderId: '',
      order: {
        user: {}
      },
    };
  },
  methods: {
    getOrder() {
      this.isLoading = true;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${this.orderId}`;
      this.$http.get(api).then((res) => {
        this.order = res.data.order;
        this.isLoading = false;
      });
    },
    payOrder() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${this.orderId}`;
      this.$http.post(api).then((res) => {
        if (res.data.success) {
          this.$bus.$emit('message:push', res.data.message, 'success');
          this.getOrder();
        } else {
          this.$bus.$emit('message:push', res.data.message, 'danger');
        }
      });
    },

  },
  created() {
    // 取得網址id
    this.orderId = this.$route.params.orderId;
  },
  mounted() {
    this.getOrder();
  }
};
</script>

<style lang="sass" scoped>

</style>


